<template>
  <flow-mapping-form
    v-if="currentItems"
    :items="items"
    :currentItems="currentItems"
    :onSave="onSave"
    :title="title"
    :onCancel="onCancel"
  ></flow-mapping-form>
</template>
<script>
import FlowMappingForm from './flowMappingForm.vue';
import { rfStreamService } from '../../../services/rfStreamsService';

export default {
  name: 'associate-object-stream-form',
  props: ['streamId', 'apiListKey', 'onCancel', 'service', 'title', 'items'],
  components: { FlowMappingForm },
  data() {
    return {
      currentItems: null,
    };
  },
  mounted() {
    this.fetchStreamById();
  },
  methods: {
    onSave(selectedItems) {
      return Promise.all(
        selectedItems.map(item => this.service.patch(item.uuid, { ...item, stream_uuid: this.streamId })),
      );
    },
    fetchStreamById() {
      return rfStreamService.fetchById(this.streamId).then(data => {
        this.currentItems = data[this.apiListKey] || [];
        return data;
      });
    },
  },
};
</script>
