<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row>
          <v-col cols="12">
            <v-autocomplete
              chips
              multiple
              clearable
              small-chips
              return-object
              item-value="uuid"
              item-text="name"
              :items="items"
              deletable-chips
              v-model="selectedItems"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit()"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  name: 'flow-mapping-form',
  props: ['currentItems', 'items', 'onSave', 'onCancel', 'title'],
  data() {
    return {
      selectedItems: [],
      isSaving: false,
      valid:false,
    };
  },
  computed: {
    lowerCaseTitle() {
      return (this.title || '').toLowerCase();
    },
  },
  mounted() {
    if (this.currentItems && this.currentItems.length > 0) {
      this.selectedItems = [...this.currentItems];
    }
  },
  destroyed() {
    this.cleanUp();
  },
  methods: {
    cleanUp() {
      this.selectedItems = [];
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onSubmit() {
      if (typeof this.onSave !== 'function') {
        throw new Error(`${this.lowerCaseTitle} form onSave function not passed`);
      }
      this.isSaving = true;
      return this.onSave(this.selectedItems, this.currentItems)
        .then(() => {
          this.$toasted.show(`Successfully mapped.`);
          this.cleanUp(true);
        })
        .catch(() => {
          this.$toasted.show('Mapping unsuccessful.', { type: 'error' });
        })
        .finally(() => {
          this.isSaving = false;
        });
    },
  },
};
</script>
