<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewProducers, item)"></text-button>
      </template>
      <template v-slot:gridItemActions="{ item, refreshGrid }">
        <icon-button
          title="Associate Flow Network"
          icon="mdi-plus"
          color="nubeYellow"
          :onClick="() => toggleDialog(dialogNames.associateFlowNetwork, item, refreshGrid)"
        ></icon-button>
      </template>
    </generic-grid>

    <v-dialog
      persistent
      max-width="500px"
      v-if="selectedItem && selectedItem.uuid"
      v-model="dialog[dialogNames.associateFlowNetwork]"
    >
      <associate-flow-network
        v-if="dialog[dialogNames.associateFlowNetwork]"
        :refreshGridCb="refreshGridCb"
        :streamId="selectedItem && selectedItem.uuid"
        :onCancel="() => closeDialog(dialogNames.associateFlowNetwork)"
      ></associate-flow-network>
    </v-dialog>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import ProducersForm from '../Producers/producersForm.vue';
import FlowNetworkStreamsForm from './flowNetworkStreamsForm.vue';
import { rfProducersService } from '../../../services/rfProducersService';
import { rfFlowNetworkStreamService } from '../../../services/rfFlowNetworkStreamService';
import AssociateFlowNetwork from '../MapStreamFlowNetwork/associateFlowNetwork.vue';

const dialogNames = {
  viewProducers: 'viewProducer',
  createProducers: 'createProducers',
  associateFlowNetwork: 'associateFlowNetwork',
};

const redirectMap = {
  [dialogNames.viewProducers]: (flowNetworkId, streamId) =>
    ROUTES.rfFlowNetworkProducer.replace(':flowNetworkId', flowNetworkId).replace(':streamId', streamId),
};
export default {
  components: {
    TextButton,
    GenericGrid,
    IconButton,
    ProducersForm,
    AssociateFlowNetwork,
    FlowNetworkStreamsForm,
  },
  name: 'flow-network-streams-grid',
  props: ['params'],
  data() {
    return {
      selectedItem: {},
      producerValues: {},
      toolbarTitle: 'Streams',
      refreshGridCb: f => f,
      gridName: gridNames.RF_FLOW_NETWORK_STREAMS,
      service: rfFlowNetworkStreamService,
      isCreatingProducer: false,
      isCreatingConsumer: false,
      isCreatingCommandGroup: false,
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.createProducers]: false,
      },
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'View',
          align: 'center',
          value: 'gridItemActions',
          sortable: false,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    flowNetworks() {
      return this.$store.state.rfFlowNetworks;
    },
    flowNetworkId() {
      return this.$route.params.flowNetworkId;
    },
  },
  methods: {
    toggleDialog(dialogName, item, refreshGridCb) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];
      this.refreshGridCb = refreshGridCb;
      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    createProducer(data) {
      this.isCreatingProducer = true;
      return rfProducersService
        .create(data)
        .then(data => {
          this.closeDialog(this.dialogNames.createProducers);
          return data;
        })
        .finally(() => {
          this.isCreatingProducer = false;
        });
    },
    redirectTo(viewName, item) {
      const streamId = item.uuid;
      if (!streamId) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(this.flowNetworkId, streamId) });
      }
    },
  },
};
</script>
