<template>
  <div>
    <flow-mapping-form
      v-if="currentItems"
      :currentItems="currentItems"
      :items="flowNetworks"
      :onSave="onSave"
      title="Associate Flow Networks"
      :onCancel="onCancel"
    ></flow-mapping-form>
    <v-overlay :value="isLoadingData">
      <v-progress-circular indeterminate size="32" color="primary"></v-progress-circular>
    </v-overlay>
  </div>
</template>
<script>
import FlowMappingForm from './flowMappingForm.vue';
import AssociateObjectStreams from './associateObjectStreams.vue';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';
import { rfStreamService } from '../../../services/rfStreamsService';

export default {
  name: 'associate-streams-form',
  props: ['streamId', 'onCancel', 'refreshGridCb'],
  components: { AssociateObjectStreams, FlowMappingForm },
  data() {
    return {
      isLoadingData: false,
      currentItems: null,
      flowNetworks: [],
    };
  },
  mounted() {
    this.fetchCurrentFlowNetworks();
    this.fetchAllFLowNetworks();
  },
  computed: {
    flowNetworkId() {
      return this.$route.params.flowNetworkId;
    },
  },
  methods: {
    onSave(data) {
      return rfStreamService.patch(this.streamId, { flow_networks: data }).then(data => {
        if (typeof this.refreshGridCb === 'function') this.refreshGridCb();
        return data;
      });
    },
    fetchAllFLowNetworks() {
      return rfFlowNetworkService.fetch().then(data => {
        this.flowNetworks = data;
        return data;
      });
    },
    fetchCurrentFlowNetworks() {
      this.isLoadingData = true;
      return rfStreamService
        .fetchById(this.streamId, { with_flow_networks: true })
        .then(data => {
          this.currentItems = data.flow_networks || [];
          return data;
        })
        .finally(() => {
          this.isLoadingData = false;
        });
    },
  },
};
</script>
