<template>
  <v-card>
    <v-card-title> Flow Network Streams </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>

      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <flow-network-streams-grid
            v-if="params.uuid"
            :params="params"
            :getDataCb="getDataCb"
          ></flow-network-streams-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import FlowNetworkStreamsGrid from './flowNetworkStreamsGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import ROUTES from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';

export default {
  name: 'flow-network-streams',
  components: {
    FlowNetworkStreamsGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        uuid: null,
        with_streams: true,
      },
      gridData: {},
      breadcrumbItems: [],
    };
  },
  mounted() {
    this.params = {
      ...this.params,
      uuid: this.$route.params.flowNetworkId,
    };
    this.setBreadCrumbs();
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
    },
    setBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push({
        text: 'Flow Network',
        align: 'center',
        disabled: true,
        link: ROUTES.rfFlowNetwork,
      });
      this.breadcrumbItems.push({ text: this.$route.params.flowNetworkId, align: 'center', disabled: false });
      this.breadcrumbItems.push({ text: 'Streams', align: 'center', disabled: true });
    },
  },
};
</script>
